import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './vuex/store'
import iView from 'iview';
import VueAMap from "vue-amap";
import './assets/scss/my-theme.less';
import './assets/scss/style.scss'
import './plugins/element.js'
Vue.use(iView);
Vue.use(VueAMap);


VueAMap.initAMapApiLoader({
    key: "a0ac46a796127cf489e1340ecf8b810a",
    plugin: [
        "AMap.Autocomplete", //
        "AMap.PlaceSearch", //POI
        "AMap.Scale", // 
        "AMap.OverView", //
        "AMap.ToolBar", //
        "AMap.MapType", //
        "AMap.PolyEditor", // 
        "AMap.CircleEditor", //
        "AMap.Geolocation",//
        "AMap.Geocoder"
    ],
    uiVersion: "1.0"
});

Vue.config.productionTip = false;

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app');
